.navbar {
    background-color: #000000;
    color: #ffffff;
    font-weight: bold;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 3;
    -webkit-box-shadow: 0px 0px 33px 5px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 33px 5px rgba(0, 0, 0, 0.75);
    font-family: 'Electrolize', sans-serif;
}




